import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let DivStyled = styled.div`
    padding : 20px;
    font-size : 45px;
`;

let H2Styled = styled.div`
font-size : 30px;
color : ${props => props.color}
`;

// class Detail2 extends React.Component{
//     componentDidMount(){
//         //detail2 컴포넌트가 Mount 되고나서 실행할 코드
//     }
//     componentWillUnmount(){
//         //detail2 컴포넌트가 UnMount 되기전에 실행할 코드
//     }
// }


function Detail(props){      

    let [alert, setAlert] = useState(true);    
    let [inputData, setInputData] = useState('');

    useEffect(()=>{
        let timer = setTimeout(() => {            
            props.setDetailModalFlag(true);
        }, 2000);        
         return (()=>{
             clearTimeout(timer);
         });
    },[alert]);
    


    let {id} = useParams(); //내가 입력한 uri > 파라미터... 0 1 2 ...  
    let history = useHistory();

    //이런 기능도 있단다... 아래 product()를 es6 문법으로 바꾼 것.
    let 찾은상품 = props.shoes.find((x)=>{
        return x.id == id;
    });

    //arrow function    
    //메서드 생성
    let product = () => {
        if(props.shoes[id].id === parseInt(id)){                        
            return props.shoes[id];
        }
    }

    //메서드 실행 > return값 받아옴
    let shoesData = product();
    
    return(
        <div className="container">                        
        <DivStyled>
            <H2Styled className='red'>Detail</H2Styled>
        </DivStyled>

        <input onChange={(e)=>{
            setInputData(e.target.value);            
        }}/>
        {inputData}
        {props.detailModalFlag === true ? null : <CountAlert />}
        
        
        {/*
            alert === true ? (<div className='my-alert3'>            
                                <p>재고가 얼마 남지 않았습니다.</p>
                                </div>)
                           : null
        */}

        <div className="row">
            <div className="col-md-6">
            {/* 아래 image는 정렬시에 문제 발생가능 해당 문제 해결해보자... */}
            <img src={props.imageUrl[id]} width="100%" alt=''/>
            </div>
            <div className="col-md-6 mt-4">
            <h4 className="pt-5">{shoesData.title}</h4>
            <p>{shoesData.content}</p>
            <p>{shoesData.price}원</p>

            {/* 재고 */}
            <Info shoesCount = {props.shoesCount}/>

            <button className="btn btn-danger" onClick={()=>{
                let copyShoesCount = [...props.shoesCount];
                copyShoesCount[0] = copyShoesCount[0] - 1;
                props.setShoesCount(copyShoesCount);
            }}>주문하기</button>&nbsp;
            <button className="btn  btn-danger" onClick={()=>{
                history.goBack();
            }}>뒤로가기</button> 
            </div>
        </div>
    </div>      
    )
}

//재고
function Info(props){
    return(
        <p>재고 : {props.shoesCount[0]}</p>
    )
}


//재고 얼마 안남았다는 modal alert 창
function CountAlert(){
    return(
        <div className='my-alert3'>            
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>
    )
};



export default Detail;
  